import React, { Component } from 'react';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';
import About from './about';
import Portfolio from './portfolio';
import Resume from './resume';
import Home from './home';
import ReactDOM from 'react-dom';

class NavBar extends Component {

    render(){
        return (
            <Router>
                <nav>
                    <ul className="nav-bar">
                        <li className="nav-link"><Link className="nav-link-text" to='/'>Home</Link></li>
                        <li className="nav-link"><Link className="nav-link-text" to={'/about'}>About</Link></li>
                        <li className="nav-link"><Link className="nav-link-text" to={'/portfolio'}>Portfolio</Link></li>
                        <li className="nav-link"><Link className="nav-link-text" to={'/resume'}>Resume</Link></li>
                    </ul>
                </nav>
                <Switch>
                    <Route path = '/about' component={About}/>
                    <Route path = '/portfolio' component={Portfolio}/>
                    <Route path = '/resume' component={Resume}/>
                    <Route path = '/' component={Home}/>
                </Switch>
            </Router>
        );
    }
}



export default NavBar;
